@import url(https://fonts.googleapis.com/css?family=Nunito);
@import url(https://fonts.googleapis.com/css2?family=Luckiest+Guy&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Luckiest+Guy&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Langar&display=swap);
*{
margin: 0;
padding:0    
}



body{
background: url('https://images.wallpaperscraft.com/image/tree_minimalism_bw_132704_3840x2160.jpg'); 
background-repeat: no-repeat;
background-size: cover;   
background-attachment: fixed;

}

#header-container{
display: flex;
justify-content: center;
}

#Footer {
    margin-top: 36vh;
    font-weight: bold;
    color: rgba(255, 255, 255, 0.719);
    font-size: 1rem;
    display: flex;
    justify-content: center;
    font-family: Nunito;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

@media (max-width: 600px) { 
    body{
        display: flex;
        justify-content: center;
        
    }


}

.CARD-MAIN-DIV{
text-align: center;

}

#deletebtn{
color: rgb(88, 88, 88);
margin-top: -2px;
font-weight: bold;
font-size: 20px;
font-family: 'Luckiest Guy', cursive;
}

.typographyTitle{
font-family: 'Luckiest Guy', cursive;
color: rgb(77, 77, 77);
}

.typographyDate{
font-size: 1rem;
}
.form-main-div {
	height: auto;
	width: 98%;
	margin-top: 2vh;
	margin: auto;
}

#wrapper {
	max-width: 100%;
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
}

.form-container {
	background: rgba(46, 46, 46, 0.644);
	margin: 48px;
	max-width: 400px;
	height: 480px;
	border-radius: 12px;
	box-shadow: 0 19px 38px rgba(0, 0, 0, 0.30), 0 15px 12px rgba(0, 0, 0, 0.22);
}

.card-list-container {
	max-width: 65%;
	display: flex;
	flex-wrap: wrap-reverse;
	padding: 0 4px;
}

#form-title {
	font-size: 1.4rem;
	color: rgba(202, 202, 202, 0.753);
	font-family: 'Luckiest Guy', cursive;
	text-align: center;
	margin-top: 28px;
	margin-bottom: 22px;
}

.inputs-container {
	width: 90%;
	margin: auto;
}

.labels {
	color: rgba(36, 36, 36, 0.438);
	font-size: 1.2rem;
	margin-left: 10px;
	font-family: 'Luckiest Guy', cursive;
}

.input-design {
	font-size: medium;
	color: white;
	margin: 8px;
	padding-bottom: 5px;
	padding-left: -10px;
	outline: none;
	font-size: 16px;
	width: 90%;
	background: transparent;
	border: solid rgba(0, 0, 0, 0.342) 0.5px;
	border-radius: 5px;
	padding: 14px;
	font-size: 1.2rem
}

:hover::-webkit-input-placeholder {
	color: rgb(226, 226, 226);
	width: 50px;
}

.btn-container {
	width: 81.5%;
	margin: auto;
	margin-top: 15px;
}

#Submit {
	margin-top: 10px;
	background-color: rgba(255, 255, 255, 0.109);
	border: rgb(0, 0, 0);
	border: solid transparent;
	border-radius: 5px;
	border-width: 0.5px;
	color: rgb(56, 56, 56);
	Padding: 10px;
	width: 100%;
	Text-align: center;
	Font-size: 1.4rem;
	Transition-duration: 0.4s;
	Font: Arial Bold;
	cursor: pointer;
	font-weight: bold;
	outline: none;
	font-family: 'Luckiest Guy', cursive;
}

#Submit:hover {
	background-color: rgba(0, 0, 0, 0.212);
	color: white;
	cursor: pointer;
}

#Submit:active {
	background-color: rgb(190, 104, 104);
}

ul {
	margin: auto;
	margin-top: 20px;
	margin-right: 50px;
}

li {
	list-style: none;
	width: 180px;
	margin: 25px;
	box-shadow: 0 19px 38px rgba(0, 0, 0, 0.30), 0 15px 12px rgba(0, 0, 0, 0.22);
	margin-right: 20px;
}

@media (max-width: 600px) {
	.form-container {
		margin-top: -5px;
		margin-left: 7.5vw;
		margin-bottom: 25px;
	}
	.card-list-container {
		margin: auto;
        max-width: 100%;
        margin: auto;
    }
    ul{
       margin-left: 10px;
    }
	li {
        margin: 15px;
	}
}

@media (max-width: 375px) {
	.inputs-container {
		width: 90%;
	}
	.input-design {
		width: 85%;
	}
}

@media (max-width: 280px) {
    
.form-container{
    width: 260px;
    margin-left: 70px;
}
.card-list-container{
margin-left: 30px;
}
	.inputs-container {
		width: 90%;
	}
	.input-design {
		width: 82%;
		font-size: 16px;
		margin-right: -15px;
	}
}
.header-main-container{
margin: 5px;
padding: 5px;
width: 80%;
height: 60px;
text-align: center;
font-family: 'Langar', cursive;
letter-spacing: 2px;
color: rgb(245, 245, 245);
background: url(/memories-ghpages/static/media/MemoriesAppHeader.4ae09545.png);
background-size: 28rem;
background-repeat: no-repeat;
background-position-x: 50%;
background-position-y: 7px;
}

#added-img{
padding: 45px;
background: url(/memories-ghpages/static/media/cameraLogo.b9fb983f.png);
background-size: 64px;
background-repeat: no-repeat;
background-position-y:  -1.5px;
background-position-x: 66.5%;
} 


@media (max-width: 1825px) {
    #added-img{
     background-position-x: 81%;

    }


}


@media (max-width: 1050px) {
    .header-main-container{
        width: 100%;
        height: 8vh;
    }
    #added-img{
        display: none;

    }
}


@media (max-width: 600px) {
    .header-main-container{
        width: 100%;
        margin-bottom: 3.5vh;
        background-size: 90vw;
    }
}
